/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";
import $ from 'jquery'
import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import ModalMessage from "../../pages/messages/ModalMessage"
import conf from '../../../conf.js'
registerLocale('es', es)


const optionGroup = [
    {
      
      options: [
        { label: "PDF", value: "PDF" },
        { label: "MP4", value: "MP4" },
        { label: "JPEG", value: "JPEG" },
        { label: "MP3", value: "MP3" },
        { label: "EXCEL", value: "EXCEL" },
        
      ]
    },
    
  ]
  const optionLangue = [
    {
      
      options: [
        { label: "Francais", value: "Francais" },
        { label: "Anglais", value: "Anglais" },
        { label: "Wolof", value: "Wolof" }
      ]
    },
   
  ]
class ModifierCompte extends Component {
    constructor(props) {
        super(props)
        this.state = {
          role:[],
          role_id:{},
          role_option:{},
          document:[
            {value:"CARTE D'IDENTE",label:"CARTE D'IDENTE"},
            {value:"PASSPORT",label:"PASSPORT"}
          ],
          document_id:"",
          ok:"",
          compte:{},
          role_update:"",
          show:false,
          success:true,
          boutton:"OK",
          message:"Compte Modifi avec succs !!",
          titre:"Message modification de Compte"

        }

    this.handleChangeRole.bind(this) 
    this.handleChangeDocument.bind(this)    
    }

   componentDidMount() {
    const myHeaders = new Headers();
    var id=localStorage.getItem("idUser")
    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    $("#chargement").css("display","none")
   
   const requestOptions = {
    headers: { 
      'Authorization':'Bearer '+data.token,

    },

    'method':'GET'
   };

   fetch(conf.url+"user/users/"+id,requestOptions)
   .then((response) => response.json())
   .then((data) => {
     console.log("data",data)
      this.setState({compte:data})
      this.setState({role_update:data.roles.libelle})
      this.setState({role_id:data.roles})
      this.setState({document_id:data.typeId})
      $("#nom").val(data.nom)
      $("#prenom").val(data.prenom)
      $("#email").val(data.email)
      $("#role").val(data.roles.libelle)
      $("#adresse").val(data.adresse)
      $("#telephone").val(data.telephone)
      $("#password").val(data.password)
      $("#naissance").val(data.naissance)
      $("#numeroId").val(data.numeroId)
      $("#typeId").val(data.typeId)
      $("#numerocompte").val(data.compteBancaire)

     })

    fetch(conf.url+"user/role/roles",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       data.map((el)=>{
        this.setState({role:[...this.state.role,{value:el.id,label:el.libelle,role:el}]})
      })

     
     }
     
    );
  }

  handleUpdateUser=()=>{
    $("#chargement").css("display","block")
    $("#initial").css("display","none")
    var id=localStorage.getItem("idUser")
    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var data= {
      "id":id,
      "username": $("#email").val(),
      "nom": $("#nom").val(),
      "prenom": $("#prenom").val(),
      "email":$("#email").val(),
      "roles":this.state.role_id,
      "adresse": $("#adresse").val(),
      "telephone": $("#telephone").val(),
      "password": $("#password").val(),
      "naissance":"15/25/2052",
      "numeroId": $("#numeroId").val(),
      "typeId": this.state.document_id,
      "monToken": this.state.compte.monToken,
      "resetPasswordToken": this.state.compte.resetPasswordToken,
      "terrains":[],
      "reservations":this.state.compte.reservations,
      "compteBancaire":$("#numerocompte").val()
     
  
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };

  fetch(conf.url+"user/update",requestOptions)
  .then((response) => response.json())
  .then((data) => {
     console.log("okkk====>",data)
     $("#chargement").css("display","none")
     $("#initial").css("display","block")
  
    try{
      if(data.id){
       this.setState({show:true,success:true});
      }else{
       this.setState({show:true,success:false});
      }
   }catch(err){
     this.setState({show:true,success:false});
   }
   }
   
  );
    
  } 

  handleChangeRole = (e) => {
    var data=[]
    var total=0
    console.log("adate =====>",e)
    try{
      this.setState({role_id:e.role})
     
    }catch(ee){

    }
  
  }
  handleChangeDocument = (e) => {
    console.log("adate =====>",e)
    try{
      this.setState({document_id:e.label})
     
    }catch(ee){

    }
  
  }

  handleValidate=()=>{
    if(this.state.success){
      this.setState({ok: <Redirect to='/liste-compte'/>});
    }else{
      this.setState({show:false})
    }
    
  }



 render() {
    const { pick } = this.state
    const { selectedGroup } = this.state
    const { selectedLangue } = this.state
    const animatedComponents = makeAnimated();

const dataTest = [
  {
    value: 1, 
    label:"Test 1"
  },
  {
    value:2, 
    label:"Test 2"
  },
  {
    value:3, 
    label:"Test 3"
  }
];

  return (
          <React.Fragment>
            <div className="page-content">

            <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />
           
            <Container fluid={true} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {this.state.ok}
                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Modifier le compte</h1>
                  {/* </CCardHeader> */}

                  <CardBody>
                    <Row> 
                      <Col md="6">
                        <label>Nom</label>
                        <Input
                              className="form-control"
                              type="text"
                              id="nom"
                              placeholder="Nom"
                          />
                      </Col>
                      <Col md="6">
                        <label>Prnom (s)</label>
                        <Input
                              className="form-control"
                              type="text"
                              id="prenom"
                              placeholder="Prnom (s)"
                          />
                      </Col>
                    </Row>
                  
                    <Row className="mt-4"> 
                      <Col md="6">
                        <label>Adresse</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="adresse"
                          placeholder="Adresse"
                        />
                      </Col>
                      <Col md="6">
                        <label>Email</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="email"
                          placeholder="Email"
                        />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="6">
                        <label>Numro de tlphone</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="telephone"
                          placeholder="Numro de tlphone"
                        />
                      </Col>
                      <Col md="6">
                        

                        <label>Date de naissance</label>
                        <div className="docs-datepicker">
                          <InputGroup>
                            <Flatpickr
                              value={pick}
                              className="form-control d-block"
                              placeholder="Date de naissance"
                              options={{
                                locale:French,
                                // mode: "range",
                                altInput: true,
                                // altFormat: "F j, Y",
                                altFormat: "d/M/Y"
                              }}
                              //  onChange={this.handleDateDebut}
                            />
                            <div className="input-group-append">
                              <button
                                type="button"
                                className="btn btn-outline-secondary docs-datepicker-trigger"
                                disabled
                              >
                                <i
                                  className="fa fa-calendar"
                                  aria-hidden="true"
                                />
                              </button>
                            </div>
                          </InputGroup>
                          <div className="docs-datepicker-container" />
                        </div>
                      </Col>
                    </Row>

                    <Row className="mt-4"> 
                      <Col md="6">
                        <label>Type de document d'identit</label> 
                        <ReactSelect
                            closeMenuOnSelect={true}
                            components={animatedComponents}
                          
                            onChange={this.handleChangeDocument}
                            options={this.state.document}

                            placeholder={this.state.compte.typeId}
                        />
                      </Col>

                      <Col md="6">
                        <label>Numro d'identit</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="numeroId"
                          placeholder={this.state.compte.numeroId}
                        />
                      </Col>
                    </Row>
                      
                    <Row className="mt-4">
                      <Col md="6">
                        <label>Mot de passe</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="password"
                          placeholder="Mot de passe"
                        />
                      </Col>
                      <Col md="6">
                        <label>Rle</label> 
                        <ReactSelect
                            closeMenuOnSelect={true}
                            components={animatedComponents}
                            
                             
                            onChange={this.handleChangeRole}
                            options={this.state.role}

                            placeholder={this.state.role_update}
                      />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="12">
                        <label>Numro de compte bancaire</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="numerocompte"
                          placeholder="Numro de compte bancaire"
                        />
                      </Col>
                    </Row>

                    <Row className="justify-content-start mt-5">
                      <Col md="3">
                        <Button id="initial"  onClick={this.handleUpdateUser}  color="info" className="b-lg" >
                          Modifier le compte
                        </Button>
                        <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button>
                      </Col>
                      <Col md="2">
                        <Button  color="secondary" className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                    </Row>

                  </CardBody>

                  
                </Card>

                {/* <hr></hr> */}

                
                {/* <Row className="justify-content-start mt-5 mb-5">
                  <Col md="6">
                    <div style={{textAlign:"center"}}>
                      <div id="echecSauv" className="alert alert-danger mt-2" role="alert">
                      </div>
                    </div>
                  </Col>
                </Row>         */}
                  </Col>
                  </Row>
                </Container>
                </div>
             
          </React.Fragment>
        )}

  }
  export default ModifierCompte